<template>
<div class="modal my-popups right fade show" id="manageProjectModal" role="dialog" style="display: block">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title"> {{ $t("ConfigureInvoiceNumbering") }} </h4>
                    <button type="button" class="close" @click="closeModal"><i class="fa fa-times" aria-hidden="true"></i> </button>
                </div>
                <div class="modal-body" style="background: white; height: auto; min-height: 0px; max-height: none;">
                    <section class="main-content">
                        <div class="container-fluid padding-t_10 p-0" id="divInvNumData">
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="preview-panel">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text" id="basic-addon3">{{$t('Preview')}}</span>
                                            </div>
                                            <input type="text" v-model="PreviewId" disabled="disabled" class="form-control" maxlength="100" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <draggable v-model="filedList" v-bind:options="{group:{ name:'people',pull:'clone', put:false },sort:false}" class="element-panel" v-bind:move="checkMove" @change="log($event)">
                                    <div class="single-element mb-2 ml-3" v-for="(field,index) in filedList" v-bind:class="field.name" style="cursor: pointer;" :key="index">
                                        <i v-bind:class="field.classname"></i> {{field.name}}
                                    </div>
                                </draggable>
                            </div>
                            <div class="row mt-2" style="overflow-y: auto;max-height: 400px;">
                                <div class="col-lg-12">
                                    <draggable class="list-group d-flex flex-wrap dragarea mt-0" v-bind:list="arr1" v-bind:options="{group:{ name:'people'}}" style="flex-direction:unset;min-height: 105px;" v-on:change="SetPreviewNumber()" id="divmanualconfigsetting">
                                        <div class="dragelements config-data" v-for="(field,index) in arr1" v-bind:key="field.name">

                                            <div class="col-12 yeardiv" v-if="field.name == 'Year'" v-bind:id="index">
                                                <div class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <label>{{field.name}}</label>
                                                        <input type="text" disabled="disabled" class="form-control form-control-sm data1" v-model="yeardata">
                                                    </div>
                                                    <i class="close fa fa-times" v-on:click="DeleteArrayObject(index,$event);yeardata=''" style="cursor: pointer;"></i>
                                                    <div class="dropdown float-right pt-1 mr-3">
                                                        <a href="javascript:;" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="action-icon dotts"><i class="fa fa-ellipsis-h text-secondary"></i></a>
                                                        <div class="dropdown-menu panelfix_dots rounded border bg-white" style="">
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="twodigit()">
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">Two Digit</span>
                                                            </a>
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="fourdigit()">
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">Four Digit</span>
                                                            </a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div class="col-12 monthdiv" v-if="field.name == 'Month'" v-bind:id="index">
                                                <div class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <label>{{field.name}}</label>
                                                        <input type="text" disabled="disabled" class="form-control form-control-sm data1" v-model="monthdata">
                                                    </div>
                                                    <i class="close fa fa-times" v-on:click="DeleteArrayObject(index,$event);monthdata=''" style="cursor: pointer;"></i>
                                                    <div class="dropdown float-right pt-1 mr-3">
                                                        <a href="javascript:;" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" class="action-icon dotts"><i class="fa fa-ellipsis-h text-secondary"></i></a>
                                                        <div class="dropdown-menu panelfix_dots rounded border bg-white" style="">
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="shortmonth()">
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">Short Name</span>
                                                            </a>
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="fullmonth()">
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">Full Name</span>
                                                            </a>
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="numbermonth()">
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">Month Number</span>
                                                            </a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div class="col-12 separatordiv" v-if="field.name == 'Separator'" v-bind:id="index">
                                                <div class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <label>Separator</label>
                                                        <input type="text" class="form-control form-control-sm data1" v-model="separatordata" maxlength="5" v-on:keyup="SetPreviewNumber" onpaste="return false;">
                                                    </div>
                                                    <i class="close fa fa-times" v-on:click="DeleteArrayObject(index,$event);separatordata=''" style="cursor: pointer;"></i>
                                                </div>
                                            </div>

                                            <div class="col-12 characterdiv" v-if="field.name == 'Character'" v-bind:id="index">
                                                <div class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <label>Character</label>
                                                        <input type="text" class="form-control form-control-sm data1" maxlength="8" onpaste="return false;" v-model="characterdata" v-on:keyup="SetPreviewNumber">
                                                    </div>
                                                    <i class="close fa fa-times" v-on:click="DeleteArrayObject(index,$event);characterdata=''" style="cursor: pointer;"></i>
                                                </div>
                                            </div>

                                            <div class="col-12 numberdiv" v-if="field.name == 'Number'" v-bind:id="index">
                                                <div class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <label>Number</label>
                                                        <input type="text" class="form-control form-control-sm data1" maxlength="10" onpaste="return false;" v-validate="'numeric'" v-model="numberdata" v-on:keyup="SetPreviewNumber">
                                                    </div>
                                                    <i class="close fa fa-times" v-on:click="DeleteArrayObject(index,$event);numberdata=''" style="cursor: pointer;"></i>
                                                    <div class="dropdown float-right pt-1 mr-3">
                                                        <a href="javascript:;" id="dropdownMenuButton" v-on:click="showdropdown" class="action-icon dotts"><i class="fa fa-ellipsis-h text-secondary"></i></a>
                                                        <div class="dropdown-menu panelfix_dots rounded border bg-white dropdivdata" style="display: none; width: 220px; right: -70px; left: auto;">
                                                            <a class="dropdown-item px-2 py-2" href="javascript:;">
                                                                <label class="form-check-inline d-block m-0 mb-1">
                                                                    <span class="label-text text-secondary mx-1" style="font-size: 14px;float: left;">Incr. By</span>
                                                                    <input type="text" class="form-control form-control-sm" v-model="exceedby" maxlength="2" v-validate="'numeric'" onpaste="return false;" v-on:keypress="isNumber($event)" style=" width: 84px;height: 25px !important;min-height: 25px !important;margin:auto" />
                                                                </label>
                                                                <label class="form-check-inline d-block m-0 mb-1">
                                                                    <span class="label-text text-secondary mx-1" style="font-size: 14px;float: left;">Reset By</span>
                                                                    <select v-model="resetby" v-on:change="Onchangeresetby">
                                                                        <option value="Month">Month</option>
                                                                        <option value="EM">Every Month</option>
                                                                        <option value="Year">Year</option>
                                                                    </select>
                                                                </label>
                                                                <label class="form-check-inline d-block m-0 mb-1" v-if="resetby == 'Month'">
                                                                    <span class="label-text text-secondary mx-1" style="font-size: 14px;float: left;">Month</span>
                                                                    <select v-model="ddlmonth" style="margin-left:15px">
                                                                        <option value="January">Jan</option>
                                                                        <option value="February">Feb</option>
                                                                        <option value="March">Mar</option>
                                                                        <option value="April">Apr</option>
                                                                        <option value="May">May</option>
                                                                        <option value="June">June</option>
                                                                        <option value="July">July</option>
                                                                        <option value="August">Aug</option>
                                                                        <option value="September">Sep</option>
                                                                        <option value="October">Oct</option>
                                                                        <option value="November">Nov</option>
                                                                        <option value="December">Dec</option>
                                                                    </select>
                                                                </label>
                                                            </a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                    </draggable>

                                </div>
                                <div class="col-lg-12">
                                    <div class="help-block">
                                        <p id="divHelpText">{{$t('HelpTextForNextInvoice')}}:</p>
                                        <span>{{$t('NextTicket')}}</span><br>
                                        <p id="divHelpTextAfterReset">{{$t('HelpTextForNextInvoiceAfterReset')}}:</p>
                                        <span>{{$t('ResetNextTicket')}}</span>
                                    </div>
                                </div>

                            </div>
                            <div class="col-lg-12 p-0">
                                <div class="row flex-row-reverse mt-3">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" class="btn btn-success formbtn widthhalf mr-2" v-on:click="SaveData()" title="Save">
                                            <i class="fa fa-save pr-2"></i>{{$t('Save')}}
                                        </a>
                                        <a href="javascript:;" v-on:click="closeModal" class="btn btn-danger formbtn widthhalf mr-2" title="Cancel"><i class="fa fa-close pr-2"></i>{{$t('Cancel')}}</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from "@/services/DataService";
import draggable from 'vuedraggable';
export default {
    components: {
        draggable,
    },
    props:{
         id: {
                type: Number,
                required: true
            },
    },
    data() {
        return {
            filedList: [{
                    name: "Year",
                    classname: "fa fa fa-calendar",
                    order: 1
                },
                {
                    name: "Month",
                    classname: "fa fa-calendar-o",
                    order: 2
                },
                {
                    name: "Separator",
                    classname: "fa fa-code",
                    order: 3
                },
                {
                    name: "Character",
                    classname: "fa fa-downcase-a",
                    order: 4
                },
                {
                    name: "Number",
                    classname: "fa fa-sort-numeric-asc",
                    order: 5
                }
            ],
            arr1: [],
            PreviewId: '',
            yeardata: '',
            monthdata: '',
            separatordata: '',
            characterdata: '',
            numberdata: '',
            exceedby: '',
            resetby: '',
            ddlmonth: '',
            isUpdated: true
        };
    },
    updated: function () {
        var vm = this;
        if (vm.isUpdated == true) {
            vm.SetPreviewNumber();
        } else {
            vm.isUpdated = true;
        }
    },
    created: function () {
        //debugger;
        var vm = this;
        vm.Fetchdata();
        vm.numbermonth();
        vm.twodigit();
        vm.fourdigit();
        vm.fullmonth();
        vm.shortmonth();
    },
    methods: {
        closeModal: function () {
            var vm = this;
            vm.$parent.ClosemodalConfigureClientBill();
        },
        showdropdown: function () {
            $(".dropdivdata").toggle();
        },
        numbermonth: function () {
            const monthNames = ["01", "02", "03", "04", "05", "06",
                "07", "08", "09", "10", "11", "12"
            ];
            const d = new Date();
            this.monthdata = monthNames[d.getMonth()];
        },
        DeleteArrayObject: function (index, e) {
            var vm = this;
            $(e.target).closest(".config-data").remove()
            vm.arr1.splice(index, 1);
            setTimeout(vm.SetPreviewNumber(), 100);
        },
        twodigit: function () {
            const d = new Date();
            this.yeardata = d.getFullYear().toString().substr(-2);
        },
        log: function (evt) {
            this.FiledList.sort(dynamicSort("order"));
        },
        fourdigit: function () {
            const d = new Date();
            this.yeardata = d.getFullYear();
        },
        fullmonth: function () {
            const monthNames = ["January", "February", "March", "April", "May", "June",
                "July", "August", "September", "October", "November", "December"
            ];
            const d = new Date();
            this.monthdata = monthNames[d.getMonth()];
        },
        shortmonth: function () {
            const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
            ];
            const d = new Date();
            this.monthdata = monthNames[d.getMonth()];
        },
        checkMove: function (evt) {
            if ($("#divmanualconfigsetting .yeardiv").length > 0 && evt.draggedContext.element.name == "Year") {
                return false;
            }
            if ($("#divmanualconfigsetting .monthdiv").length > 0 && evt.draggedContext.element.name == "Month") {
                return false;
            }
            if ($("#divmanualconfigsetting .numberdiv").length > 0 && evt.draggedContext.element.name == "Number") {
                return false;
            }
            if ($("#divmanualconfigsetting .characterdiv").length > 0 && evt.draggedContext.element.name == "Character") {
                return false;
            }
            if ($("#divmanualconfigsetting .separatordiv").length > 0 && evt.draggedContext.element.name == "Separator") {
                return false;
            }
        },
        SetPreviewNumber: function (evt) {

            var vm = this;
            //vm.arr1.push(evt.added.element);
            this.isUpdated = false;
            this.PreviewId = '';
            var data = $(".data1");
            var prev = '';
            $.each(data, function (index, element) {
                prev = prev + $(element).val();
            });
            this.PreviewId = prev;
        },
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        Fetchdata: function () {
            var vm = this;
            vm.isLoading=true;
           var params = `id=${vm.id}`
            DataService.GetConfigNumberJsonData(params).then((response) => {
                if (response.data != null || typeof (response.data) != 'undefined' || response.data != '' || response.data == "") {
                    var jsonArray = response.data.data;
                    var sortarray = jsonArray;
                    sortarray.splice("order", -1);

                    $.each(sortarray, function (i, obj) {
                        if (obj.type == 'Year') {
                            vm.yeardata = obj.value;
                            if (vm.yeardata != "") {
                                var obj1 = {};
                                obj1["name"] = obj.type;
                                vm.arr1.push(obj1);
                            }
                        }
                        if (obj.type == 'Month') {
                            vm.monthdata = obj.value;
                            if (vm.monthdata != "") {
                                var obj2 = {};
                                obj2["name"] = obj.type;
                                vm.arr1.push(obj2);
                            }
                        }
                        if (obj.type == 'Separator') {
                            vm.separatordata = obj.value;
                            if (vm.separatordata != "") {
                                var obj3 = {};
                                obj3["name"] = obj.type;
                                vm.arr1.push(obj3);
                            }
                        }
                        if (obj.type == 'Character') {
                            vm.characterdata = obj.value;
                            if (vm.characterdata != "") {
                                var obj4 = {};
                                obj4["name"] = obj.type;
                                vm.arr1.push(obj4);
                            }
                        }
                        if (obj.type == 'Number') {
                            vm.numberdata = obj.value;
                            vm.exceedby = obj.propvalue1;
                            vm.resetby = obj.property2;
                            vm.ddlmonth = obj.propvalue2;
                            if (vm.numberdata != "") {
                                var obj5 = {};
                                obj5["name"] = obj.type;
                                vm.arr1.push(obj5);
                            }
                        }
                        if (obj.type == "PreviewNum") {
                            vm.PreviewId = obj.value;
                        }
                    
                    });
                }
             vm.isLoading=false;
            });
        },
        SaveData: function () {
            var vm = this;
             vm.isLoading=true;
            if (vm.PreviewId != '') {
                var prop = '';
                var propm = '';
                var propexceed = '';
                var propreset = '';
                var propvalue2 = ''
                var formatyear = '';
                var formatmonth = '';
                if (vm.yeardata.length == 2) {
                    prop = 'two-digit';
                    formatyear = vm.yeardata
                } else {
                    prop = 'four-digit';
                    formatyear = vm.yeardata
                }
                if (vm.monthdata.length > 3) {
                    propm = 'full-month';
                    formatmonth = vm.monthdata;
                } else if (vm.monthdata.length == 2) {
                    propm = 'number-month';
                    formatmonth = vm.monthdata;
                } else {
                    propm = 'short-month';
                    formatmonth = vm.monthdata;
                }
                if (vm.exceedby != '') {
                    propexceed = 'exceedby';
                }
                if (vm.resetby == 'EM') {
                    propreset = vm.resetby;
                    propvalue2 = '';
                } else if (vm.resetby == 'Month') {
                    propreset = vm.resetby;
                    propvalue2 = vm.ddlmonth;
                } else {
                    propreset = vm.resetby;
                    propvalue2 = '';
                }
                if (vm.resetby == 'Month' && vm.ddlmonth == '') {
                    this.ShowAlert("Please Select Month", "warning", true, ("Alert"));
                    return false;
                }
                if (vm.numberdata == '') {
                    this.ShowAlert("Number is required", "warning", true, ("Alert"));
                    return false;
                }
                var prev1 = '',
                    prev2 = '',
                    prev3 = '',
                    prev4 = '',
                    prev5 = '';
                var yearorder = typeof ($(".yeardiv").attr("id")) == 'undefined' ? "-1" : $(".yeardiv").attr("id");;
                var separator = typeof ($(".separatordiv").attr("id")) == 'undefined' ? "-1" : $(".separatordiv").attr("id");
                var character = typeof ($(".characterdiv").attr("id")) == 'undefined' ? "-1" : $(".characterdiv").attr("id");
                var number = typeof ($(".numberdiv").attr("id")) == 'undefined' ? "-1" : $(".numberdiv").attr("id");
                var month = typeof ($(".monthdiv").attr("id")) == 'undefined' ? "-1" : $(".monthdiv").attr("id");

                var mainarray = [{
                        type: 'Year',
                        value: vm.yeardata,
                        property1: prop,
                        propvalue1: vm.yeardata,
                        order: yearorder,
                        format: formatyear
                    },
                    {
                        type: 'Month',
                        value: vm.monthdata,
                        property1: propm,
                        propvalue1: vm.monthdata,
                        order: month,
                        format: formatmonth
                    },
                    {
                        type: 'Separator',
                        value: vm.separatordata,
                        property1: '',
                        propvalue1: '',
                        order: separator,
                        format: vm.separatordata
                    },
                    {
                        type: 'Character',
                        value: vm.characterdata,
                        property1: '',
                        propvalue1: '',
                        order: character,
                        format: vm.characterdata
                    },
                    {
                        type: 'Number',
                        value: vm.numberdata,
                        property1: propexceed,
                        propvalue1: vm.exceedby,
                        property2: propreset,
                        propvalue2: propvalue2,
                        order: number,
                        format: vm.numberdata
                    },
                    {
                        type: 'PreviewNum',
                        value: vm.PreviewId,
                        resetvalue: vm.numberdata
                    }
                ];

                var configType = {
                    Configtype: "InvoiceStartNumber"
                }

                $.each(mainarray, function (i, obj) {
                    if (obj.order == '0') {
                        prev1 = obj.format;
                    }
                    if (obj.order == '1') {
                        prev2 = obj.format;
                    }
                    if (obj.order == '2') {
                        prev3 = obj.format;
                    }
                    if (obj.order == '3') {
                        prev4 = obj.format;
                    }
                    if (obj.order == '4') {
                        prev5 = obj.format;
                    }
                })
                var prevformat = prev1 + prev2 + prev3 + prev4 + prev5;
                var previewnumformat = {
                    PreviewNumFormat: prevformat
                }
                var previewnum = {
                    PreviewNum: vm.PreviewId
                }
                var serialnumber = {
                    SerialNum: vm.numberdata
                }

                var postedata = {
                    mainarray: mainarray,
                    configtype: configType,
                    previewnum: previewnum,
                    previewnumformat: previewnumformat,
                    serialnumber: serialnumber
                }

                var objectPostString = JSON.stringify(postedata);
                DataService.SaveConfiguringJsonData(objectPostString).then((response) => {
                    //debugger;
                    if (response.data == 1) {
                        this.ShowAlert("Saved successfully", "success", true, ("Alert"));
                        this.closeModal(prevformat);
                        //this.$emit('close', prevformat);
                        vm.$emit('activateTab');
                         vm.isLoading=false;
                    } else if (response.data == 0) {
                        this.ShowAlert("Update successfully", "success", true, ("Alert"));
                        this.closeModal(prevformat);
                        //this.$emit('close', prevformat);
                        vm.$emit('activateTab');
                         vm.isLoading=false;
                    }
                }, function (response) {});
            } else {
                ShowAlert("Preview is required", "warning", true, ("Alert"));
            }
           
        },
    }
}
</script>
